body {
  font-family: "Roboto", sans-serif;
  padding: 0;
  margin: 0;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.highlight pre {
  max-width: 960px;
  margin: 0 auto; }

code {
  font-size: 1.3rem; }

.wrapper {
  max-width: 960px;
  margin: 0 auto; }

strong {
  font-weight: 500; }

header {
  padding: 10px; }

.text-center {
  text-align: center; }

.subline {
  font-size: 2rem;
  font-weight: 100; }

.presentation {
  background: rgba(52, 73, 94, 0.7);
  color: #fff;
  padding: 40px; }

h1, h2 {
  font-family: "Shadows Into Light", sans-serif; }

p {
  font-size: 1rem;
  line-height: 1.6; }

.button, button {
  font: inherit;
  font-weight: 400;
  background: #95a5a6;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  transition: background .5s ease, transform .5s ease;
  margin: 10px;
  display: inline-block;
  text-decoration: none; }
  .button:hover, button:hover {
    background: #b1bdbd;
    transform: translate3d(0, 4px, 0); }
  .button > *, button > * {
    pointer-events: none; }
  .button.alt, button.alt {
    background: #9b59b6; }
    .button.alt:hover, button.alt:hover {
      background: #b07cc6; }
  .button.warn, button.warn {
    background: #e74c3c; }
    .button.warn:hover, button.warn:hover {
      background: #ed7669; }
  .button.twitter, button.twitter {
    background: #00aced; }
    .button.twitter:hover, button.twitter:hover {
      background: #21c2ff; }

.marking {
  transition: background .5s ease;
  background: #2ecc71; }

.deleted {
  transition: background .5s ease;
  background: none !important; }

.marking .marking {
  background: #25a25a; }

.marking .marking .marking {
  background: #1b7943; }

::selection {
  background: #7ee2a8; }

pre {
  margin: 0; }

pre > code {
  display: block;
  font-family: monospace;
  overflow-y: auto;
  padding: 30px;
  -webkit-overflow-scrolling: touch; }

.tight {
  margin-bottom: 0; }

@media (max-width: 480px) {
  .presentation {
    padding: 15px; }
  .hide-small {
    display: none; } }

